@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
/* DefaultLayout.css */

/* Logo */
/* .logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.3);
}   */
.logo {
display: flex;
align-items: center;
justify-content: center;
height: 100px;
color: aliceblue;
transition: all 0.2s;
}
.logo h3{
  margin: 0;
  font-size: 20px;
  font-weight: bold;
  padding-top: 50px;
  padding-bottom: 100px;
  transition: all 0.2s;
}
/* Content */
.content {
  margin: 24px 16px;
  padding: 24px;
  min-height: 650px;
  background: #f0f2f5;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Sider */
.ant-layout-has-sider {
  padding: 5px !important;
}

.ant-layout.ant-layout-has-sider {
  min-height: 650px;
  height: auto;
}

.ant-layout-sider {
  border-radius: 10px !important;
  padding-top: 10px !important;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Anticon */
.anticon svg {
  height: 20px;
  width: 20px;
  font-size: 20px;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

/* Header */
.ant-layout-header {
  line-height: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 0 16px;
  height: 60px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Cart Item */
.cart-item {
  padding-left: 20px;
  padding-right: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.cart-item p {
  font-size: 1rem;
  margin-top: 13px;
  font-weight: 700;
  margin-left: 10px;
}

/* Item selectet hover  */
/* .ant-menu-item-selected{
  background-color: #EA7C69 !important;
} */
#invoice-POS {
  box-shadow: 0 0 1in -0.25in rgba(0, 0, 0, 0.5);
  padding: 4mm;
  margin: 0 auto;
  width: 80mm;
  background: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

::selection {
  background: #f31544;
  color: #fff;
}
::moz-selection {
  background: #f31544;
  color: #fff;
}
h1 {
  font-size: 1.5em;
  color: #222;
}

h3 {
  font-size: 1em;
  font-weight: 300;
  line-height: 2em;
}
p {
  font-size: 0.8rem;
  font-weight: 500;
  color: #666;
  line-height: 1.2em;
}

#top,
#mid,
#bot {
  /* Targets all id with 'col-' */
  border-bottom: 1px solid #eee;
}

#top {
  min-height: 100px;
  background-color: #f7f7f7;
  padding: 10px;
}

#bot {
  min-height: 50px;
  background-color: #f7f7f7;
  padding: 10px;
}

.title {
  text-align: center;
  float: right;
  font-size: 1rem;
  font-weight: bold;
}
.title p {
  text-align: center;
}
table {
  width: 100%;
  border: 1px solid #ccc;
  margin-top: 0px;
}
th {
  padding: 8px;
  border-top: 1px solid#ccc;
  border-left: 1px solid#ccc;
  border-right: 1px solid#ccc;
  font-size: 1.1rem;
  font-weight: 700;
}
td {
  padding: 8px;
  border-top: 1px solid #ccc;
  border-left: 1px solid#ccc;
  border-right: 1px solid#ccc;
  border-bottom: 1px solid #ccc;
  font-size: 1rem;
  font-weight: 500;
}
.payment h6,
.payment h5 {
  text-align: center;
}

.Rate.table-header h6,
.Rate.table-header h5 {
  text-align: center;
}

.tabletitle {
  padding: 8px;
  background: #eee;
  text-align: center;
  border: none;
}

.service td {
  border-bottom: 1px solid #eee;
  text-align: center;
}
.item {
  width: 24mm;
}
.itemtext {
  font-size: 0.9em;
  font-weight: 500;
}

#legalcopy {
  margin-top: 5mm;
  font-size: 0.7em;
  text-align: center;
}

body {
  font-family: "Orbitron", sans-serif;
}

.item-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
}

.item-button button {
  background-color: #1890ff;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  padding: 2px 12px;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 0px;
}

.item-button button:hover {
  background-color: white;
  color: black;
  border: 2px solid black;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.spinner-border {
  height: 50px;
  width: 50px;
}

.category {
  border: 2px solid gray;
  margin-right: 25px;
  margin-bottom: 30px;
  padding: 10px;
  cursor: pointer;
}

.category img {
  margin-left: 10px;
}

.category-active {
  border: 2px solid gray;
  background-color: rgba(128, 128, 128, 0.501);
}

/*register page*/

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.register-page form {
  width: 400px;
  background-color: #fff;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.register-page form .d-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.register-page form .d-flex a {
  color: #007bff;
  text-decoration: none;
}

.register-page h1 {
  text-align: center;
  margin-bottom: 20px;
}

.register-page .ant-form-item {
  margin-bottom: 20px;
}

.register-page .ant-form-item-label {
  font-weight: bold;
}

.register-page .ant-form-item-explain {
  color: #ff4d4f;
}

.register-page .ant-form-item-has-error .ant-input {
  border-color: #ff4d4f;
}

.register-page .ant-form-item-has-error .ant-input:focus {
  box-shadow: 0 0 0 2px rgba(255, 77, 79, 0.2);
}

.register-page .btn-primary {
  width: 100%;
}

